.about {
  display: flex;
  gap: 88px;
  max-width: 1160px;
  margin-bottom: 120px;

  @media (max-width: $tablet-width) {
    gap: 31px;
    margin-bottom: 97px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    gap: 25px;
    margin-bottom: 65px;
  }
}

.about__wrapper-text {
  width: 45%;
  padding: 0;
  padding-top: 55px;
  margin: 0;

  @media (max-width: $tablet-width) {
    width: 48%;
    padding-top: 0;
    padding-left: 3px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    margin-bottom: 7px;
  }
}

.about__wrapper-text h2 {
  padding: 0;
  margin: 0;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: $text-dark-blue;

  @media (max-width: $tablet-width) {
    width: 64%;
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 15px;
  }

  @media (max-width: $mobile-width) {
    width: 66%;
    font-size: 24px;
    line-height: 34px;
  }
}

.about__wrapper-text p {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $text-dark-blue;
}

.about__image {
  width: 48%;
  min-height: 301px;
  padding: 0;
  margin: 0;
  background: none;

  @media (max-width: $tablet-width) {
    min-height: 264px;
    padding-top: 10px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    min-height: 186px;
    padding-top: 0;
  }
}

.about__image img {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  object-fit: contain;
  background-color: #b1bccd;
  vertical-align: top;
}
